import React from 'react';
import { Epic, Panel, Tabbar, ConfigProvider, HeaderButton, IS_PLATFORM_IOS, Div, Link, Alert, Switch, CellButton,ModalCard, Gallery, Avatar, FormLayoutGroup, FormLayout, Checkbox, Textarea, SelectMimicry, Radio, Input, UsersStack, IS_PLATFORM_ANDROID, TabbarItem, PanelHeader, ScreenSpinner, View, ModalPage, ModalPageHeader, ModalRoot, InfoRow, Group, HorizontalScroll, Tappable, Search, List, Cell, Button} from '@vkontakte/vkui';

import BookDetail from './BookDetail';
import AuthorDetail from './AuthorDetail';

import PropTypes from 'prop-types';

import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon24Search from '@vkontakte/icons/dist/24/search';
import Icon28Message from '@vkontakte/icons/dist/28/message';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';



import './books.css';
import books from '../books/books.json';
import author from '../author/author.json';


import vkuiConnect from '@vkontakte/vkui-connect';


function openTab(url) {

// Create link in memory

let a = window.document.createElement("a");

a.target = '_blank';

a.href = url;

// Dispatch fake click

let e = window.document.createEvent("MouseEvents");

e.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);

a.dispatchEvent(e);

};


const itemStyle = {
    flexShrink: 0,
    width: 80,
    height: 94,
    display: 'flex',
    flexDirection:
    'column',
    alignItems: 'center',
    fontSize: 12
  };




const book = Object.keys(books).map(i => { 
	return books[i]; 
	})


class Home  extends React.Component {
	constructor (props,) {
	  super(props);
  
		this.state = {
			activeStory: 'feed',
			bookId: null,
			popout: null,
			search: '',
			contextOpened: true,
			mode: 'all',
			activePanel: 'feed',
		    activeModal: null,
            modalHistory: [], 
            history: ['feed']
		};
    this.onChange = this.onChange.bind(this);
    this.onStoryChange = this.onStoryChange.bind(this);
	this.toggleContext = this.toggleContext.bind(this);
    this.select = this.select.bind(this);
  }
   

    
	goBack = () => {
    const history = [...this.state.history];
    history.pop();
    const activePanel = history[history.length - 1];
    if (activePanel === 'feed') {
      vkuiConnect.send('VKWebAppDisableSwipeBack');
    }
    this.setState({ history, activePanel });
	}
	
	goForward = (activePanel) => {
    const history = [...this.state.history];
    history.push(activePanel);
    if (this.state.activePanel === 'feed') {
      vkuiConnect.send('VKWebAppEnableSwipeBack');
    }
    this.setState({ history, activePanel });
  }

	toggleContext () {
		this.setState({ contextOpened: !this.state.contextOpened });
	  }
	
	select (e) {
		const mode = e.currentTarget.dataset.mode;
		this.setState({ mode });
		requestAnimationFrame(this.toggleContext);
	  }

	onChange (search) { this.setState({ search }); }
	
	get book () { 
		const search = this.state.search.toLowerCase();
		return book.filter(({forpoisk}) => forpoisk.toLowerCase().indexOf(search) > -1);
	}

	
	go = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to })
	}

	backauthor = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to })
		window.scrollTo(0,0)
	}





	onStoryChange (e) {
		this.setState({ popout: <ScreenSpinner /> });
        setTimeout(() => { this.setState({ popout: null }) }, 2000);
		this.setState({ activeStory: e.currentTarget.dataset.story })
		window.scrollTo(0,0)
	}

	selectBook = id => {
		this.setState({ bookId: id, activeStory: "bookDetail"})
		window.scrollTo(0,0)
	}
	
	selectAuthor = id => {
		this.setState({ bookId: id, activeStory: "authorDetail"})
		window.scrollTo(0,0)
	}
	render () {
     
    
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
				this.state.activeStory !== 'bookDetail' &&
				this.state.activeStory !== 'authorDetail' &&
			<Tabbar>
			<TabbarItem
				onClick={this.onStoryChange}
				selected={this.state.activeStory === 'feed'}
				data-story="feed"
			><Icon24Newsfeed/></TabbarItem>
			<TabbarItem
				onClick={this.onStoryChange}
				selected={this.state.activeStory === 'discover'}
				data-story="discover"
			><Icon24Search/></TabbarItem>
			<TabbarItem
				onClick={() => openTab("https://vk.me/club185912225")}
			><Icon24Add/></TabbarItem>
			</Tabbar>
		}>
			<View id="feed" activePanel="feed" >
			<Panel id="feed">
				<PanelHeader>Рецепты</PanelHeader>
		<Group className="Header__content" title="Кулинарный справочник"> 
				<HorizontalScroll style={{ padding: '0px 2px'}} children={''}>
					<div style={{ display: 'flex' }}>
						{
							Object.keys(author).map(i => 
								<Tappable 
									onClick={() => this.selectAuthor(i)} 
									style={{borderRadius: 15, margin: 2}}
								>
									
									<div style={itemStyle}>
              <Avatar size={64} src={author[i].path} style={{ marginBottom: 8 }}></Avatar>
             {author[i].title}
                          </div>
								</Tappable>
							)
						}
					</div>
					</HorizontalScroll>
		</Group>
							<Group className="Header__content">
					<List>
						{
							Object.keys(books).map(i => <Cell
								onClick={() => this.selectBook(i)}
								key={i}

							>
							<span>{books[i].title}</span>
							<span className="disable-selection text">Кухня: {books[i].kuxnya} </span>
							<span className="disable-selectionn text">Категория: {books[i].category}</span>
							</Cell>

							)
						}
					</List> 
				</Group>
				 <Div>
     </Div>
			</Panel>
			</View>
<View id="info" activePanel="info" >
			<Panel id="info">
				<PanelHeader>Пожаловатья</PanelHeader>
				<Group>
              <List>
                <Cell expandable onClick={this.go} data-to="otvet">Недостоверная информация</Cell>
                <Cell expandable onClick={this.go} data-to="otvet">Отсутвие фотографий</Cell>
                <Cell expandable onClick={this.go} data-to="otvet">Недостаточно информации</Cell>
              </List>
            </Group>

			</Panel>
			</View>
			<View id="otvet" activePanel="otvet" >
			<Panel id="otvet">
				<PanelHeader></PanelHeader>
				<Group>
<Div align="center">					
Спасибо! Мы обязательно рассмотрим жалобу и примем необходимые действия! 
</Div>
            </Group>

			</Panel>
			</View>
			<View id="discover" activePanel="discover">
			<Panel id="discover">
			<PanelHeader>Найти рецепт</PanelHeader>
				<Search value={this.state.search} onChange={this.onChange}/>
				<List>
				{ 
this.book.map((book,r) => ( 
	<Cell 
	key={book.id} 
	onClick={() => this.selectBook(book.id)} 
	>{book.title}</Cell> 
	)).sort((a,b) =>{ return a-b })}
				</List>
				</Panel>
			</View>
			<View id="bookDetail" activePanel="bookDetail" onSwipeBack={() => this.setState({activePanel: "feed"})}>
				<BookDetail id="bookDetail" go={this.go} bookId={this.state.bookId} bookStartId={1} />
			</View>
			<View id="authorDetail" activePanel="authorDetail" history={this.state.history} onSwipeBack={this.goBack} >
				<AuthorDetail id="authorDetail" backauthor={this.backauthor} bookId={this.state.bookId} bookStartId={1} />
			</View>
		</Epic>
	)}
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	backauthor: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;