import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Gallery, Div, List, Cell, platform, Alert, Link, HeaderContext, Header, PanelHeaderContent, HeaderButton, IOS} from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';

import '@vkontakte/vkui/dist/vkui.css';

import books from '../books/books.json';
import vkuiConnect from '@vkontakte/vkui-connect';



import Icon24Document from '@vkontakte/icons/dist/24/document';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';
import Icon24FavoriteOutline from '@vkontakte/icons/dist/24/favorite_outline';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Globe from '@vkontakte/icons/dist/24/globe';
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24RecentOutline from '@vkontakte/icons/dist/24/recent_outline';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import Icon24LinkCircle from '@vkontakte/icons/dist/24/link_circle';



import './books.css';


function openTab(url) {

// Create link in memory

let a = window.document.createElement("a");

a.target = '_blank';

a.href = url;

// Dispatch fake click

let e = window.document.createEvent("MouseEvents");

e.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);

a.dispatchEvent(e);

};

class BookDetail extends React.Component {
	constructor (props) {
		super(props);

		this.state = {
			bookText: null,
			activeStory: "home",
			check: false,
			popout: null,
			contextOpened: false,
            mode: 'all',
			activePanel: 'feed',
            history: ['feed'],
			page: this.props.bookStartId
		};
    this.toggleContext = this.toggleContext.bind(this);
    this.select = this.select.bind(this);
	}
	  

	goBack = () => {
		const history = [...this.state.history];
		history.pop();
		const activePanel = history[history.length - 1];
		if (activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppDisableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  share = () => { 
	  	connect.send("VKWebAppShowWallPostBox", {"message": `Привет! Я нашел рецепт "${books[this.props.bookId].title}" Остальное смотри в сервисе - vk.com/app7116412`  });
	  }

	toggleContext () {
       this.setState({ contextOpened: !this.state.contextOpened });
      }

    select (e) {
       const mode = e.currentTarget.dataset.mode;
       this.setState({ mode });
       requestAnimationFrame(this.toggleContext);
      }

	  goForward = (activePanel) => {
		const history = [...this.state.history];
		history.push(activePanel);
		if (this.state.activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppEnableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  go = (e) => { 
		this.setState({ activePanel: e.currentTarget.dataset.to })
		};
		
		backauthor = (e) => { 
			this.setState({ activeStory: e.currentTarget.dataset.to })
			window.scrollTo(0,0)
		}



		

	getBookText = (id,glava) => {
		console.log(require("../books"+books[id].preview))
		fetch(require("../books/content"+require("../books"+books[id].preview)[glava].text))
		.then(text => text.text())
		.then(data => {this.setState({bookText: data})})
		this.setState({page: glava, check: true})
		let status = window.navigator.onLine ? true : false;
if(!status) {alert("Проверьте интернет соединение.\nПовторите попытку");}
	}
	
	
	render () {
		const osname = platform();
		return (
			<Panel id={this.props.id}>
				    <PanelHeader
            left={<HeaderButton onClick={this.props.go} data-to="feed" > {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
          >
            <PanelHeaderContent  status={books[this.props.bookId].title} aside={<Icon16Dropdown />} onClick={this.toggleContext}>
              Рецептник
            </PanelHeaderContent>
          </PanelHeader>
          <HeaderContext opened={this.state.contextOpened} onClose={this.toggleContext}>
            <List>
              <Cell
                before={<Icon24Newsfeed />}
                onClick={this.props.go} data-to="info"
              >
                Пожаловаться
              </Cell>
            </List>
          </HeaderContext>

								  <Group title="Фотографии">
              <Gallery
                style={{ height: 200 }}
                bullets="custom"
              >
               <img type="image"  src={books[this.props.bookId].foto1} />
              </Gallery>
            </Group>
                  <Group title="Информация о рецепте:">
              <List>
                <Cell indicator={books[this.props.bookId].author} before={<Icon24User fill="#ff0000" />}>Автор</Cell>
                <Cell indicator= <Link href = {books[this.props.bookId].istok} component ="a" >{books[this.props.bookId].sait}</Link> before={<Icon24LinkCircle fill="#ff0000" />}>Источник</Cell>
                <Cell indicator= {books[this.props.bookId].kuxnya} before={<Icon24Globe fill="#ff0000" />}>Кухня</Cell>
                <Cell indicator=  {books[this.props.bookId].rate} before={<Icon24FavoriteOutline fill="#ff0000" />}>Рейтинг</Cell>
                <Cell indicator= {books[this.props.bookId].time} before={<Icon24RecentOutline fill="#ff0000" />}>Время приготовления</Cell>
                <Cell indicator= {books[this.props.bookId].category} before={<Icon24Filter fill="#ff0000" />}>Категория</Cell>
              </List>
            </Group>
				<Group title ="Ингредиенты:">
					<Div>
					   {books[this.props.bookId].ingerd1} 
					  <br /> {books[this.props.bookId].ingerd2} 
					  <br /> {books[this.props.bookId].ingerd3} 
					  <br /> {books[this.props.bookId].ingerd4} 
					  <br /> {books[this.props.bookId].ingerd5} 
					  <br /> {books[this.props.bookId].ingerd6} 
					  <br /> {books[this.props.bookId].ingerd7} 
				</Div>
				</Group>
				<Group title ="Рецепт">
					 <Div>
					<br />1) {books[this.props.bookId].rezept1}
<br />
					<br />2) {books[this.props.bookId].rezept2}
<br />
					<br />3) {books[this.props.bookId].rezept3} 
<br />
					<br />4) {books[this.props.bookId].rezept4} 
<br />
					<br />5) {books[this.props.bookId].rezept5}
<br />
					<br />6) {books[this.props.bookId].rezept6}
				</Div>
				 <Div>
       <Button size="xl" onClick={this.share} level="commerce" before={<Icon24Newsfeed/>}  level="secondary">Поделиться</Button>
     </Div>
				</Group>

				  <Group>
				<Header level="2" aside={<Link 	onClick={() => openTab("https://vk.me/club185912225")} >Написать</Link>}>Комментарии</Header>
  
			<Div align="center">					
Комментариев еще нет. Станьте первым! 
</Div>
     

      				</Group>
			</Panel>
        );
    }
}
BookDetail.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


export default (BookDetail);

